.social-sharing {
  display: flex;
  align-items: center;

  &__list {
    display: flex;
    flex: 1;
    justify-content: space-between;
    min-width: 260px;
    max-width: 400px;
    margin: 0 auto;
    padding-top: 12px;

    @include breakpoint($desktop) {
      padding: 0;
    }
  }

  &__list-item {
    line-height: 1;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    @extend %icon-only;
    border: 2px solid $purple;
    border-radius: 50%;
    display: block;
    height: 40px;
    text-align: center;
    width: 40px;
    color: $white;

    &:active,
    &:hover {
      background-color: lighten($purple, 15%);
      text-decoration: none;
      color: #fff !important;

      &.icon-facebook {
        background-color: palette(brand, facebook);
      }

      &.icon-twitter {
        background-color: palette(brand, twitter);
      }

      &.icon-linkedin {
        background-color: palette(brand, linkedin);
      }

      &.icon-google-plus {
        background-color: palette(brand, googleplus);
      }

      &.icon-pocket {
        background-color: palette(brand, pocket);
      }

      &.icon-pinterest {
        background-color: palette(brand, pinterest);
      }
    }

    &:hover {
      border-color: transparent;
    }
  }

  .sticky-article-nav & {
    max-width: 80%;
    margin: auto;

    @include breakpoint($desktop) {
      float: right;
      max-width: 30%;
      width: 100%;
      height: 70px;
    }

    &__link {
      color: $white;
    }
  }
}

.share {
  .icon--circle {
    &.icon-facebook {
      background-color: palette(brand, facebook);
    }

    &.icon-twitter {
      background-color: palette(brand, twitter);
    }

    &.icon-linkedin {
      background-color: palette(brand, linkedin);
    }

    &.icon-googleplus {
      background-color: palette(brand, googleplus);
    }

    &.icon-pocket {
      background-color: palette(brand, pocket);
    }

    &:hover,
    &:active {
      color: #fff;
    }
  }
}
