.styleguide {
  .row {
    display: flex;
    align-items: center;

    &.top {
      align-items: flex-start;
    }

    &.stretch {
      align-items: stretch;
    }
  }

  .fonts {
    .robotoslab,
    .mark-OT {
      margin-bottom: 5rem;
      font-weight: 400;
    }

    .robotoslab {
      font-family: $sans;
    }

    .mark-OT {
      font-family: $heading;
    }

    .italic {
      font-style: italic;
    }

    .bold {
      font-weight: 700;
    }

    .medium {
      font-weight: 500;
    }

    .thin {
      font-weight: 100;
    }

    .light {
      font-weight: 300;
    }

    .specimen {
      color: #000;

      .heading,
      .char {
        text-align: right;
      }

      .char {
        font-size: 6em;
        line-height: 1.4;
      }

      .text {
        font-size: 1.8rem;
      }
    }
  }

  .iconset {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    margin: 3rem 0;
    justify-content: center;

    .ico-example {
      flex: 0 1 auto;
      display: flex;
      flex-direction: column;
      // padding: .75em .25em .25em;
      // padding: .75em;
      margin: .5em;
      border: 1px solid #ddd;
      border-radius: 8px;
      width: 200px;
      min-height: 160px;
      background-color: #fff;
    }

    .icon {
      display: block;
      margin: 0 auto;
      width: 50px;
      text-align: center;

      &:before {
        margin: 0;
        font-size: 38px;
        line-height: 50px;
      }
    }

    .list-bare {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      > li {
        margin: .5em;
      }
    }

    code {
      font-size: .8em;
      display: inline-block;
      padding: 0 .5em;
    }
  }

  .buttons {
    .row {
      margin-bottom: 1em;
    }

    .example {
      text-align: center;
    }
  }

  .article--basic {
    padding-bottom: 0;
  }
}

