.content-sharing {
  position: relative;

  .story--blank & {
    position: absolute;
    top: -10px;
    right: -10px;
    z-index: 10;
  }

  button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    transform: translate3d(25%, -25%, 0);
  }
}

[data-module='contentSharing'] {
  position: relative;
  margin-top: 40px;

  .button,
  .share-icons {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-in-out;
  }

  .button {
    transform: translate3d(50%, -50%, 0) scale(0);
    transform-origin: center;
  }

  &:hover,
  &.open {
    .button {
      transform: translate3d(50%, -50%, 0) scale(1);
      visibility: visible;
      opacity: 1;
    }
  }

  .share-icons {
    transform: translate3d(50%, 0, 0) scale(0);
    transform-origin: top center;
  }

  &.open {
    .share-icons {
      transform: translate3d(50%, 50px, 0) scale(1);
      visibility: visible;
      opacity: 1;
    }
  }

  .social-sharing__list {
    flex-direction: column;
    min-width: 0;
  }

  .social-sharing__list-item {
    margin-bottom: 1em;
  }

  .social-sharing__link {
    background-color: rgba($purple, .8);
  }
}
