.button-wrapper--cta-full {
  display: block;
  margin-bottom: 15px;
  width: 100%;
  text-align: center;
  overflow: hidden;

  > * {
    position: relative;
    width: 100%;

    @include breakpoint($tablet) {
      width: 50%;
    }

    @include breakpoint($desktop) {
      width: 30%;
    }

    &:after,
    &:before {
      display: none;

      @include breakpoint($tablet) {
        background-color: darken($gray-light, 10%);
        content: '';
        display: block;
        height: 1px;
        position: absolute;
        top: 50%;
        width: 100%;
      }
    }

    &:after {
      right: -2em;
      transform: translate3d(100%, -50%, 0);
    }

    &:before {
      left: -2em;
      transform: translate3d(-100%, -50%, 0);
    }
  }

  .button {
    @include font-size(14);
    border: 1px solid darken($gray-light, 10%);
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
    font-weight: 700;
    letter-spacing: 1px;
    outline: 10px solid transparent;
    padding: 1.25em 1em;
    text-transform: uppercase;

    &.more {
      color: $purple-action;
      background-color: transparent;

      &:hover {
        background-color: darken($gray-light, 10%);
      }
    }
  }
}
