//=====================================
//    #CORE/HTML - LINKS
//=====================================

a {
  @include transition;
  text-decoration: none;
  color: $purple-action;

  .fonts-loaded & {
    font-weight: 400;
  }

  &:active {
    text-decoration: underline;
    color: $purple;

    .category--blue & {
      color: $blue;
    }

    .category--green & {
      color: $green;
    }

    .category--teal & {
      color: $teal;
    }

    .category--plum & {
      color: $plum;
    }
  }

  &:hover {
    color: darken($purple-action, 15%);
  }
}
