.related-stories {
  border-top: 1px solid #dedede;
  display: inherit;
  margin: 0 0 30px;
  padding: 30px 0;

  h3 {
    @include breakpoint($desktop) {
      margin-bottom: 0;
    }
  }

  .stories-container {
    padding: 2% 0;
  }

  .card-view {
    @include breakpoint(801px $desktop) {
      .story__content {
        height: 100%;
        max-height: none;
      }
    }

    .story {
      width: span(12);

      @include breakpoint(801px) {
        width: span(4);
      }
    }

    .story__description {
      font-size: 1.4rem;
    }

    .video-player {
      @include aspect-ratio(4, 3);

      @include breakpoint($tablet) {
        @include aspect-ratio(9, 10.4);
      }
    }
  }
}
