//=====================================
//    #UTILITY - COLORS
//=====================================

// Note: the palette function is included here (vs in the tools directory)
// since both of these needed in order to work properly

@function palette($palette, $tone: 'base') {
  @return map-get(map-get(map-get($palettes, colors), $palette), $tone);
}

$palettes: (
  colors: (
    black: (
      base: #211b26
    ),
    orange: (
      base: #dc8b27
    ),
    white: (
      base: #fff
    ),
    gray: (
      base: #544a5b,
      light: #f2f2f2,
      medium: #e4e4e4,
      dark: #666,
      button: #e5e5e5
    ),
    blue: (
      base: #0f4b91,
      bright: #1464c2
    ),
    green: (
      base: #799a3d
    ),
    teal: (
      base: #3d9a95
    ),
    purple: (
      action: #7b3eb3,
      base: #5b2e82,
      dark: #3e1052
    ),
    plum: (
      base: #863375
    ),
    brand: (
      facebook: #3b5998,
      twitter: #55acee,
      linkedin: #0077b5,
      googleplus: #dc4e41,
      pocket: #ef4056,
      pinterest: #bd081c
    ),
    red: (
      error: #e35d5d,
      errorBg: #ffe7e7
    ),
    yellow: (
      base: #f2c80f
    )
  )
);

$white: palette(white);
$black: palette(black);
$gray: palette(gray);
$gray-light: palette(gray, light);
$gray-medium: palette(gray, medium);
$gray-dark: palette(gray, dark);
$blue: palette(blue);
$blue-bright: palette(blue, bright);
$green: palette(green);
$teal: palette(teal);
$purple: palette(purple);
$purple-action: palette(purple, action);
$purple-dark: palette(purple, dark);
$plum: palette(plum);
$ruby: palette(plum);
$orange: palette(orange);
$yellow: palette(yellow);

$error: palette(red, error);
$errorBg: palette(red, errorBg);
$highlight: palette(blue, bright);

$copy: palette(gray, base);
