.category-view-toggle {
  height: 100%;

  &__list {
    margin: 0;
    padding: 0 2%;

    @include breakpoint($tablet) {
      float: right;
      padding: 0;
      // margin-top: .5rem;
    }

    &:after {
      display: block;
      clear: both;
      content: '';
    }
  }

  &__list-item {
    @include span(6);
    display: inline-block;

    &:last-child {
      @include last;
    }
  }

  &__button {
    display: block;
    padding: 10px;
    width: 100%;
    border-color: $gray-medium;
    background-color: transparent;
    color: $black;
    text-align: center;
    cursor: pointer;

    @include breakpoint($tablet) {
      margin: .5em;
      padding: 0;
      width: 48px;
      height: 48px;
      border-color: transparent;
      background-color: $black;
      color: $white;
      text-indent: -9999;

      &:hover {
        background-color: $purple-action;
      }
    }

    &:before {
      @include font-size(20);
      margin: 0 !important;
    }

    &:active,
    &[aria-pressed='true'] {
      background-color: $gray-light;
      color: $purple;

      @include breakpoint($tablet) {
        background-color: $gray-dark;
        color: $white;

        &:hover {
          background-color: $gray;
          cursor: default;
        }
      }
    }
  }
}
