.form-group {
  &.email {
    label {
      margin-left: 0;
    }

    [type='email'] {
      padding: 1.25em;
      padding-right: 112px;
      color: $black;
      font-weight: 300;
    }

    // input[type='submit']
    [type='submit'] {
      position: absolute;
      top: 0;
      right: 0;
      background-color: $purple-action;
      color: #fff;
      font-weight: 300;
    }

  }
}

.email-signup {
  &__heading {
    @include font-size(30, 35);
    color: $white;
    font-weight: 400;
  }

  &__description {
    @include font-size(16, 21);
    padding: 0;

    .flyout-menu & {
      padding-bottom: .5em;
    }
  }

  input {
    transition: all .3s ease;
    border-color: $purple;
  }

  // button[type='submit'],
  // input[type='submit']
  [type='submit'] {
    background-color: $purple-action;

    &:focus,
    &:hover {
      background-color: darken($purple-action, 15%);
    }
  }

  .sidebar &,
  .off-canvas-slide-in &,
  &--cta-full {
    @include dots-bg($fixed: false);
    background-color: $purple;
    padding: 2rem;
    color: $white;
  }

  &--cta-full,
  &--flyout,
  .sidebar & {
    input {
      &:focus {
        box-shadow: 0 0 1px 1px lighten($purple-action, 15%);
        outline-color: lighten($purple-action, 15%);
      }
    }
  }

  &--cta-full {
    @include breakpoint($desktop) {
      padding: 40px 30px;

      .email-signup--description {
        font-weight: 200;
        margin-bottom: 5px;
        margin-top: 5px;
      }
    }

    &:before {
      background-attachment: fixed;
    }

    h3 {
      @include font-size(40, 47);
      color: $white;
      font-weight: 400;

      @include breakpoint($desktop) {
        @include font-size(47, 56);
        margin: 0;
      }
    }
  }

  &--flyout {
    margin: 1em 2em 1.5em;
  }

  &--footer {
    input {
      border-color: #d5d5d5;
      background-color: $gray-light;
    }
  }
}
