.primary-navigation {
  &__list {
    margin: 0;
  }

  &__list-item {
    font-size: 18px;
    border-bottom: 1px solid darken($purple, 5%);
  }

  &__link {
    @include transition();
    @include icon(arrow-right, after, true);
    display: block;
    padding: 1em 1.5em;
    position: relative;

    .sub-menu & {
      padding-left: 3em;
      background-color: darken($purple, 2.5%);
      font-size: .9em;
    }

    &:hover {
      background-color: darken($purple, 5%);
    }

    &:active,
    &:focus {
      background-color: darken($purple, 10%);
    }

    &:after {
      transition: all .6s ease-in-out;
      position: absolute;
      display: block;
      right: 1em;
      top: 50%;
      transform: translate3d(-50%, -50%, 0);
      font-size: .8em;
    }

    @include breakpoint($tablet) {
      &:hover:after {
        animation: .5s ico-bounce ease-in alternate;
        animation-iteration-count: infinite;
      }
    }
  }

  .sub-menu {
    margin-left: 0;
  }
}


@keyframes ico-bounce {
  to {
    transform: translate3d(-100%, -50%, 0);
  }
}
