.cards-container {
  border-bottom: 1px solid darken($gray-light, 15%);
  border-top: 1px solid darken($gray-light, 15%);
  display: block;
  margin: 40px auto;
  max-width: 1260px;
  padding: 30px 2rem;
  position: relative;
  z-index: 1;

  @include breakpoint($tablet) {
    padding: 30px 4rem;
  }

  &__title-section {
    display: inline-block;
    width: 100%;

    h3 {
      text-align: center;
      font-weight: 400;
    }

    p {
      text-align: center;
    }
  }

  .swiper-container {
    margin-left: -2rem;
    margin-right: -2rem;
    overflow: hidden;

    @include breakpoint($large-tablet) {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .swiper-pagination {
    padding: 25px 0 0;
    position: relative;
    // width: 100%;

    &-bullet {
      margin: 0 7.5px;
      height: 10px;
      width: 10px;
    }

    &-bullet-active {
      background-color: $purple-action;
      box-shadow: none;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    @include transition;
    display: none;

    @include breakpoint(1430px) {
      display: block;
      background-color: darken($gray-light, 5%);
      border-radius: 50%;
      font-size: 1.8rem;
      height: 80px;
      width: 80px;
      margin-top: 0;
      z-index: 2;
    }

    &:hover {
      background: darken($gray-light, 15%);
    }

    &:before {
      color: $purple-action;
      font-weight: bold;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
    }
  }

  .swiper-button-disabled {
    // opacity: .3;
    box-shadow: none;
  }

  .swiper-button-prev {
    left: 0;
    transform: translateX(-60px);
    // left: -60px;
    // right: inherit;

    // &:before {
    //   left: -2px;
    //   right: inherit;
    // }
  }

  .swiper-button-next {
    right: 0;
    transform: translateX(60px);
    // left: inherit;
    // right: -60px;

    // &:before {
    //   left: inherit;
    //   right: -2px;
    // }
  }
}
