//=====================================
// String Replace
//=====================================
@function str-replace($string, $search, $replace: '') {
  $index: str-index($string, $search);

  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }

  @return $string;
}

//=====================================
// Font Face
//=====================================
@mixin font-face($name, $path, $weight: null, $style: null, $exts: woff2 woff ttf) {
  $src: null;
  $extmods: (eot: '?', svg: '#' + str-replace($name, ' ', '_'));
  $formats: (otf: 'opentype', ttf: 'truetype');

  @each $ext in $exts {
    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
    $src: append($src, url(quote($path + '.' + $extmod)) format(quote($format)), comma);
  }

  @font-face {
    font-family: quote($name);
    font-style: $style;
    font-weight: $weight;
    src: $src;
  }
}

// Usage:
//
//  @include font-face($name, $path, $weight, $style);
//
// Define $weight and $style only if other than `400, normal`)

// Icons
@include font-face('icomoon', '../fonts/icomoon/icomoon');

// mark-OT
@include font-face('mark-OT', '../fonts/mark-OT/mark-OT-Light', 300);
@include font-face('mark-OT', '../fonts/mark-OT/mark-OT', 400);
@include font-face('mark-OT', '../fonts/mark-OT/mark-OT-Medium', 500);
@include font-face('mark-OT', '../fonts/mark-OT/mark-OT-Bold', 700);

// Roboto Slab
@include font-face('robotoSlab', '../fonts/roboto-slab/RobotoSlab-Thin', 100);
@include font-face('robotoSlab', '../fonts/roboto-slab/RobotoSlab-Light', 300);
@include font-face('robotoSlab', '../fonts/roboto-slab/RobotoSlab-Regular', 400);
@include font-face('robotoSlab', '../fonts/roboto-slab/RobotoSlab-Bold', 700);


$fallbackSans: Helvetica, Arial, sans-serif;

$sans: 'robotoSlab', $fallbackSans;
$heading: 'mark-OT', $fallbackSans;

$icomoon: 'icomoon';

$fontFeatures-sans: 'kern' on, 'liga' on, 'calt' on, 'ss01', 'ss04', 'case', 'lnum';
$fontFeatures-heading: 'kern' on, 'liga' on, 'calt' on, 'ss02', 'lnum';
$fontFeatures-numbers: 'kern' on, 'liga' on, 'calt' on, 'ss02', 'lnum';

