.explore-navigation {
  background-color: darken($purple, 5%);

  p {
    // @include font-size(14);
    color: rgba($white, .4) !important;
    padding: 10px 30px;
  }

  &__list {
    @include container();
    margin: 0;
  }

  &__list-item {
    // @include span(1 of 3 inside no-gutters);
    font-size: 14px;
    text-align: center;
    flex: 1;
    border: 1px solid $purple;

    &:first-child {
      border-left: 0;
    }

    &:last-child {
      border-right: 0;
    }
  }

  &__link {
    @include transition();
    // color: $white;
    display: block;
    padding: 27px 5px;
    position: relative;

    &.icon:before,
    &:before {
      display: block;
      font-size: 1.5em;

      @include breakpoint($tablet) {
        margin-right: .5em;
        display: inline-block;
      }
    }

    &:hover {
      background-color: $purple-action;
    }


  }
}
