//=====================================
//    #CORE/HTML - LISTS
//=====================================
@mixin ol-custom($name: ol) {
  list-style: none;
  counter-reset: #{$name}-counter;
  margin: 0;

  li {
    position: relative;
    margin-bottom: 1em;
    padding-left: 2em;

    &:before {
      content: counter(#{$name}-counter);
      counter-increment: #{$name}-counter;
      font-feature-settings: 'onum';
      position: absolute;
      font-family: $heading;
      font-weight: 400;
      font-size: 150%;
      top: 0;
      left: 0;
    }
  }
}

ul,
ol,
dl {
  margin-left: $spacing-unit * 2;
}

.ol-custom {
  @include ol-custom();
}

.list-bare,
.list-inline {
  list-style: none;
  margin: 0;
}

.list-bare {
  padding: 0;
}

.list-inline {
  display: inline-block;

  > li {
    display: inline-block;
  }
}

.list-flex {
  display: flex;
  flex-direction: row;

  &.list-bare {
    flex-direction: column;
  }
}

