.off-canvas-slide-in {
  @include transition();
  @include dots-bg($fixed: false);
  position: fixed;
  right: 0;
  top: 200px;
  transform: translate3d(100%, 0, 0);
  width: 340px;
  z-index: 100;

  &.slide-in {
    transform: translate3d(0, 0, 0);
  }

  .dismiss {
    @include font-size(20);
    color: $white;
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
    margin: .5em;
    padding: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 101;

    &:hover {
      background: transparent;
      cursor: pointer;
      font-weight: 700;
    }
  }

  .content {
    position: relative;
    // background-color: $purple;
    // padding: 30px;
  }

  h3 {
    color: $white;
    font-weight: 400;
  }

  p {
    @include font-size(16);
  }


