//=====================================
//    #CORE/HTML - TYPOGRAPHY
//=====================================

p {
  @include font-size(16, 26);
  margin-bottom: 1.3em;
  font-feature-settings: 'kern' on, 'liga' on, 'calt' on, 'ss01', 'ss04', 'case', 'lnum';

  @include breakpoint($desktop) {
    @include font-size(18, 28);
  }
}

.lede {
  .article--basic & {
    @include font-size(19, 31);
    font-feature-settings: 'kern' on, 'liga' on, 'calt' on;
    font-family: $heading;
    font-weight: 300;
    margin-bottom: 1em;
    letter-spacing: -.025em;

    @include breakpoint($desktop) {
      @include font-size(30, 42);
    }
  }

  .article--advanced & {
    &:first-letter {
      float: left;
      padding-bottom: 0;
      font-size: 4em;
      line-height: .9;
      font-family: $heading;
      font-weight: 500;
      padding-right: .2em;
    }
  }
}

.drop-cap {
  display: block;
  font-size: 4em;
  line-height: .8;
  font-family: $heading;
  font-weight: 500;
  position: absolute;
  top: 0;
  left: .1em;
}

blockquote {
  @include font-size(15, 27);
  display: block;
  position: relative;
  font-weight: bold;
  margin-bottom: .5em;
  padding-left: 1.5em;

  @include breakpoint($desktop) {
    @include font-size(20, 30);
  }

  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 5%;
    height: 95%;
    border-left: 4px solid;

    @include breakpoint($desktop) {
      border-left: 6px solid;
    }
  }

  p {
    max-width: none !important;
  }
}

.thin {
  .fonts-loaded & {
    font-weight: 100;
  }
}

small,
.small,
.font_small {
  font-size: .8em;
  line-height: 1.5;
  // font-size: .618rem;
}

abbr,
.smcaps {
  text-transform: uppercase;
  font-feature-settings: 'kern' 1, 'liga' 1, 'calt' 1, 'pnum' 1, 'tnum' 0, 'onum' 1, 'lnum' 0, 'smcp' 1, 'c2sc' 1;
}

pre,
kbd,
samp,
code {
  font-feature-settings: 'kern' 0, 'liga' 0, 'calt' 1, 'dlig' 0, 'pnum' 0, 'tnum' 1, 'onum' 0, 'lnum' 1, 'zero' 1;
}

code {
  background-color: $gray-medium;
  color: $gray-dark;

  pre & {
    display: block;
    overflow: auto;
  }
}

pre {
  white-space: pre-wrap;
}

hr {
  height: 1rem;
  margin: 1rem 0;
}

.highlight {
  background-color: $highlight;
  box-shadow: 0 1px 0 2px $highlight;
  color: #fff;
}

sup {
  font-feature-settings: 'kern' 1, 'liga' 1, 'calt' 1, 'pnum' 1, 'tnum' 0, 'onum' 1, 'lnum' 0, 'dlig' 0, 'sups' 1;
}

sub {
  font-feature-settings: 'kern' 1, 'liga' 1, 'calt' 1, 'pnum' 1, 'tnum' 0, 'onum' 1, 'lnum' 0, 'dlig' 0, 'subs' 1;
}

::selection {
  background-color: $purple-action;
}

