.page-hero {
  background-position: 50% 0;
  background-size: cover;
  background-color: $purple;
  overflow: hidden;
  z-index: 2;

  // Color is coded off of the body tag.
  .category--blue & {
    background-color: $blue;

    &:after {
      background-image: linear-gradient($blue, transparent);
    }
  }

  .category--green & {
    background-color: $green;

    &:after {
      background-image: linear-gradient($green, transparent);
    }
  }

  .category--plum & {
    background-color: $plum;

    &:after {
      background-image: linear-gradient($plum, transparent);
    }
  }

  .category--teal & {
    background-color: $teal;

    &:after {
      background-image: linear-gradient($teal, transparent);
    }
  }

  &__button {
    @include font-size(14);
    border: 1px solid $white;
    background-color: transparent;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: all .2s ease;

    @include breakpoint($tablet) {
      padding: 15px 30px;
    }

    &:hover {
      border-color: transparent;
      box-shadow: 0 0 0 0 transparent;
    }

    &:active,
    &:focus {
      box-shadow: 0 12px 8px -6px $black;
      text-shadow: 0 4px 8px $black;
    }
  }

  .home &__inner {
    width: 100%;
    top: auto;
    left: 0;
    bottom: 0;
    transform: translate3d(0, 0, 0);
    background-color: rgba(0, 0, 0, .5);
    padding: 2rem 0;
    margin-top: 25%;
    position: relative;

    @include breakpoint($tablet) {
      padding-top: 4rem;
    }

    @include breakpoint($desktop) {
      padding-top: 6rem;
      position: absolute;
      margin-bottom: 2rem;
      margin-top: 0;
      width: 50%;

      &:after {
        position: absolute;
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        top: 0;
        right: -100%;
        background: linear-gradient(to right, rgba(0, 0, 0, 0.5), transparent 50%);
      }
    }

  }

  &__wrapper {
    display: block;

    &:hover,
    &:focus,
    &:active {
      text-decoration: none;
      box-shadow: none;
    }
  }

  &__title,
  &__subtitle,
  &__description,
  &__byline {
    color: $white;

    .fonts-loaded & {
      font-family: $heading;
      font-feature-settings: $fontFeatures-heading;
    }
  }

  &__title {
    transition: all .3s ease-out;

    .page-hero__wrapper:hover &,
    .page-hero__wrapper:active &,
    .page-hero__wrapper:focus & {
      color: palette(gray, button);
    }
  }

  &__byline {
    padding-top: 1.3em;
    border-top: 1px solid $white;
    font-size: 16px;
    font-weight: 600;
    color: $gray-medium;
  }

  &__subtitle {
    margin: 0;
  }

  &--big {
    // position: relative;
    // min-height: 70vh;

    // @include aspect-ratio(4, 3);
    // max-height: 50vh;

    // @include breakpoint($tablet) {
    // }

    @include breakpoint($desktop) {
      @include aspect-ratio(16, 9);
      max-height: 80vh;
    }

    &:after {
      display: block;
      position: absolute;
      content: '';
      top: auto;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0;
      height: 2rem;
      background-color: $purple;
    }

    .page-hero__inner {
      height: auto;
      z-index: 5;
    }

    .page-hero__title {
      @include breakpoint($large-mobile) {
        // @include font-size(35, 45);
        // margin: 0 .5em .5em;
        font-size: 4rem;
        line-height: 1;
        margin: 0 0 .5em;
      }

      @include breakpoint($tablet) {
        // @include font-size(50, 60);
      }

      @include breakpoint($desktop) {
        // @include font-size(66, 75);
        // margin: 0 2em .5em;
      }
    }

    .page-hero__description,
    .page-hero__subtitle {
      // @include font-size(18, 25);
      font-size: 2.2rem;
      line-height: 1.2;

      // @include breakpoint($tablet) {
      //   @include font-size(22, 32);
      // }
    }
  }

  &--small {
    @include dots-bg;
    padding: 50px 2rem;
    text-align: center;

    .wrapper {
      max-width: 780px;
      padding: 0;
    }

    .page-hero__title {
      margin: 0 .5em;
      font-weight: 400;

      @include breakpoint($desktop) {
        margin: 0 2em;
      }
    }

    .page-hero__description,
    .page-hero__subtitle {
      margin: 1em 0 0;
    }
  }

  &--gradient {
    &:before {
      background-image: linear-gradient(to left, $purple, rgba($gray, .8));
    }
  }
}
