.q-and-a {
  margin: 3rem auto;

  .answer,
  .question {
    @include font-size(16, 26);
    padding: 0;
    position: relative;
    margin-bottom: 1.5em;
    padding-left: 90px;

    @include breakpoint($desktop) {
      @include font-size(18, 28);
    }

    &:before {
      position: absolute;
      font-size: 2em;
      border-right: 2px solid rgba($copy, .5);
      font-family: $heading;
      font-feature-settings: $fontFeatures-heading;
      font-weight: 400;
      height: 92%;
      width: 60px;
      left: 0;
      top: 4%;
      color: $purple;
    }
  }

  .question {
    font-weight: 700;

    &:before {
      content: 'Q';
    }
  }

  .answer {

    &:before {
      content: 'A';
    }
  }
}
