// TAGS
.tags {
  padding: 15px 0;
  border-top: 1px solid;

  &__label {
    @include font-size(16, 16);
    color: $copy;
    margin: 0;
    display: inline-block;
  }

  &__list {
    margin-left: .5em;
    vertical-align: bottom;
  }

  &__list-item {
    font-weight: 700;

    &:before {
      content: ', ';
    }

    &:first-child {
      &:before {
        content: none;
      }
    }
  }
}
