.category-tool-bar {
  position: relative;
  z-index: 1;
  border-bottom: 1px solid $gray-medium;
  color: $white;
  cursor: default;

  @include breakpoint($tablet) {
    height: 64px;
    border: 0;
    background-color: $black;
  }

  .wrapper {
    margin-top: 0;
    margin-bottom: 0;
  }

  &__inner {
    @include breakpoint($tablet) {
      margin: 0 auto;
      max-width: 1260px;
    }
  }

  &__filters {
    height: 60px;
    background-color: $black;

    @include breakpoint($tablet) {
      @include span(8);
      height: 100%;
      background-color: transparent;
    }
  }

  &__filter {
    @include span(6);
    border-right: 1px solid rgba($gray-medium, .1);

    &:last-child {
      @include last;
      border: 0;
    }

    select {
      margin-top: .5rem;
      width: 97%;
      border: 0;
      background-color: transparent;

      @include breakpoint($tablet) {
        margin-top: 1rem;
      }
    }
  }

  &__view-toggle {
    height: 100%;
    background-color: $white;

    @include breakpoint($tablet) {
      @include span(4 last);
      background-color: transparent;
    }
  }
}
