.interactive {
  // margin: 30px 0;
  max-width: 1260px;

  &__content {
    background-color: $white;

    @include breakpoint($tablet) {
      padding: 30px;
    }

    @include breakpoint($desktop) {
      padding: 60px;
    }
  }

  &__label,
  &__description {
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
  }

  &__label {
    @include font-size(24);
    font-weight: 500;
  }

  &__heading {
    @include font-size(37.5);
    font-weight: 400;

    @include breakpoint($desktop) {
      @include font-size(51);
    }
  }

  &__description {
    @include font-size(16, 18);

    @include breakpoint($desktop) {
      @include font-size(19, 24);
      margin-right: 3em;
    }
  }
}
