.pullquote {
  @include font-size(20);
  position: relative;
  border: 1px solid $gray-medium;
  border-left: 0;
  border-right: 0;
  text-align: center;
  margin: ($spacing-unit * 2) auto;
  font-weight: 500;
  font-family: $heading;
  font-feature-settings: $fontFeatures-heading;

  @include breakpoint($desktop) {
    @include font-size(40, 44);
    margin: ($spacing-unit * 2) auto ($spacing-unit * 3);
  }

  .article--advanced &:not(&__wide) {
    @include breakpoint($desktop) {
      max-width: 80%;
    }
  }

  .quote {
    padding: ($spacing-unit * 2) $spacing-unit;
  }

  .icon--circle {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate3d(-50%, 50%, 0) scale(.8);
    margin: 0;
    font-size: 24px;

    // @include breakpoint($desktop) {
    //   transform: translate3d(-50%, 50%, 0) scale(1);
    // }
  }

  &__wide {
    background-color: $blue-bright;
    color: #fff;

    .quote {
      position: relative;
      margin: 2em 0 0;
      padding: 2em 0;

      @include breakpoint($desktop) {
        margin-top: 1em;
      }

      &:before {
        content: '';
        display: block;
        position: absolute;
        height: 2.5em;
        width: 2.5em;
        transform: translate3d(-25%, -75%, 0);
        background-image: url('../images/quotation-mark.svg');
        background-repeat: no-repeat;
        opacity: .2;
      }
    }

    .icon--circle {
      top: 0;
      right: 0;
      left: auto;
      bottom: auto;
      transform: translate3d(-25%, 25%, 0) scale(.8);

      // @include breakpoint($desktop) {
      //   transform: translate3d(-25%, 25%, 0) scale(1);
      // }
    }
  }

  .interactive & {
    margin: 3rem auto 1rem;
    border: 0;
    text-align: left;
    text-indent: -.5em;

    .quote {
      padding: 2rem;
      font-weight: 400;

      @include breakpoint($desktop) {
        margin-left: .5em;
      }
    }
  }
}

.citation {
  margin: 2em auto 0;

  @include breakpoint($desktop) {
    margin: 1em auto 0;

    .quote & {
      width: 60%;
    }
  }

  .image {
    border-radius: 100%;
    overflow: hidden;
    max-width: 4em;
    margin-right: 2rem;

    @include breakpoint($desktop) {
      max-width: 100px;
    }
  }

  .inner {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }

  .attribution {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;

    p {
      margin: 0 !important;
      max-width: none !important;
      text-align: left;

      &:not(.name) {
        font-size: 16px;
        font-weight: 300;
      }
    }

    .name {
      font-size: 20px;
    }
  }
}
