.generic-content {
  // background: $white;

  .wp-content {
    z-index: 1;
    overflow: hidden;
    position: relative;
    padding: 30px 0 50px;

    .wrapper {
      max-width: 1060px;
    }

    p,
    h2,
    h3,
    h4,
    ul,
    blockquote,
    table {
      margin-left: auto;
      margin-right: auto;
      max-width: 660px;
    }

    li {
      // UL margin is set to 'auto', set margin on LI instead
      margin-left: 4em;
    }

    table {
      img {
        margin-bottom: 0;
        margin-top: 0;
      }
    }
    // iframe {
    //   display: block;
    //   height: 250px;
    //   margin: 30px auto;
    //   width: 100%;

    //   @include breakpoint($tablet) {
    //     height: 450px;
    //   }
    // }

    img {
      display: block;
      margin: auto;
      width: 100%;

      @include breakpoint($desktop) {
        height: auto;
      }
    }
  }
}
