.sidebar {
  margin-top: 30px;
  padding-left: 0;

  @include breakpoint($desktop) {
    margin-top: 0;
    padding-left: 2em;
  }

  &--component {
    will-change: margin-left, position, top, width, z-index;
  }

  + .spacer {
    @include breakpoint(max-width 1000px) {
      display: none !important;
      height: 0 !important;
      width: 0 !important;
    }
  }
}
