.search-toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  border-right: 1px solid #eee;
  text-align: center;

  @include breakpoint($tablet) {
    width: 70px;
    height: 70px;
  }

  &:focus {
    box-shadow: 0 0 15px -5px $purple;
  }

  &__icon {
    color: $purple-action;
    display: block;
    position: relative;
    height: 100%;
    padding: 18px;
    font-size: 2rem;
    margin: 0;
    border: 0;
    border-radius: 0;

    @include breakpoint($tablet) {
      padding: 24px;
      font-size: 2.4rem;
    }

    &,
    &:hover,
    &:active {
      background-color: #fff;
    }

    &:focus:not(:active) {
      box-shadow: 0 0 15px -5px $purple;
    }

    &:hover {
      color: darken($purple-action, 15%);
      text-decoration: none;
    }
  }
}
