.poll {
  background-color: $white;
  box-shadow: 1px 1px 0 #e5e5e5;
  margin: 1em auto;

  @include breakpoint($tablet) {
    width: 80%;
  }

  @include breakpoint($large-tablet) {
    width: 50%;
    float: right;
    margin: 0;
    margin-left: 2em;
  }

  // @include breakpoint($desktop) {
  //   width: 35%;
  // }
}

.wp-polls,
.wp-polls-form {
  height: 100%;
  background-color: $white;
  color: $black;
}

.wp-polls-form,
.wp-polls-ans {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.wp-polls-ans {
  margin: 0;
  // flex: 1;
  // justify-content: flex-end;
}

.wp-polls ul,
.wp-polls-ul,
.wp-polls-ul li,
.wp-polls-ans ul {
  margin: 0;
  list-style: none;
  padding: 3%;
}

.wp-polls-ul {
  li {
    @include transition(.3s);
    // margin-bottom: 5px;

    &:hover {
      background-color: $gray-light;
    }
  }

  .control {
    margin: 0;
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
    color: $copy;
    display: block;
    padding-left: 2.5em;
  }
}

.wp-polls {
  .headline,
  .description {
    font-weight: 300;
    text-align: center;
    color: $copy;
    margin: 0;
    padding: 3% 3% 0;
  }

  .headline {
    @include font-size(34, 50);
  }

  .description {
    @include font-size(18);
  }

  .total-voters {
    text-align: center;
    color: $copy;
    margin: 0 0 1em;
    font-size: .9em;
  }

  .results {
    display: none;
  }

  [type='button'] {
    transition: all .3s ease-in-out;
    padding: 16px 2rem;
    border-top: 2px solid $gray-light;
    border-radius: 0;
    font-size: 1em;
    font-weight: 700;
    cursor: pointer;
    margin-bottom: 0;

    &.button--clear {
      color: $purple-action;
    }

    &:hover,
    &:active,
    &:focus {
      color: $purple-action;
    }

    &:hover,
    &:active,
    &.learn-more {
      border-top-color: transparent;
    }

    &:focus {
      box-shadow: inset 0 -4px 2px -2px $purple;
    }

    &:hover {
      background-color: darken($gray-light, 10%);
    }

    &:active {
      background-color: darken($gray-light, 20%);
    }

    &.learn-more {
      color: $white;
      background-color: $purple-action;
      text-align: center;
    }
  }

  .pollbar {
    margin: 10px 0;
    height: 8px;
    background-color: $purple;
    max-width: 0;
    animation: 1s ease-in 1s forwards pollbar;
  }
}

@keyframes pollbar {
  0% {
    max-width: 0;
  }

  100% {
    max-width: 100%;
  }
}


&.correct,
&.incorrect {
  .control {
    input:checked ~ .control-indicator {
      border: 0;
    }
  }
}

&.correct {
  background-color: rgba($green, .2);

  .control {
    input:checked ~ .control-indicator {
      background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCA4IDgiPjxwYXRoIGZpbGw9IiNGRkYiIGQ9Ik02LjQgMWwtLjcuNy0yLjggMi44LS44LS44LS43LS43TDAgNC40bC43LjcgMS41IDEuNS43LjcuNy0uNyAzLjUtMy41LjctLjdMNi40IDF6Ii8+PC9zdmc+);
      background-color: $green;
    }
  }
}

&.incorrect {
  background-color: rgba($error, .2);

  .control {
    input:checked ~ .control-indicator {
      background-image: url(data:image/svg+xml;base64,);
      background-color: $error;
    }
  }
}
