.thumbs {
  margin-bottom: 1em;

  &__label {
    @include font-size(18, 48);
    color: $copy;
    margin: 0;
    display: none;
  }

  // &__list {
  //   margin-left: .5em;
  //   vertical-align: middle;
  // }
  // .button {
  //   color: $purple-action;
  // }

  .vortex-container-vote {
    display: inline-block;
    padding: 10px 0px;
  }

  .vortex-p-like,
  .vortex-p-dislike {
    color: $purple-action;
    cursor: pointer;

    &:before {
      font-size: 28px;
    }
  }

  .vortex-container-like,
  .vortex-container-dislike,
  .vortex-container-like-comment,
  .vortex-container-dislike-comment {
    display: inline-block;
    margin-left: 1em;
  }

  .vortex-p-like-counter,
  .vortex-p-dislike-counter {
    margin-left: 1rem;
  }
}
