.search-bar {
  transition: all .25s ease-out;
  background-color: $gray-light;
  display: block;
  z-index: 5;
  border: 1px solid #e4e4e4;

  .global-header & {
    top: 60px;
    left: 0;
    right: 0;
    max-height: 0;
    overflow: hidden;
    position: fixed;
    visibility: hidden;
    opacity: 0;
    box-shadow: inset 0 5px 5px -5px rgba($black, .15);
    border: 0 solid transparent;

    @include breakpoint($tablet) {
      top: 70px;
    }

    &.open {
      transition: all .5s ease-out;
      opacity: 1;
      visibility: visible;
      height: 50px;
      max-height: 50px;
      border-bottom: 2px solid rgba($black, .1);

      @include breakpoint($tablet) {
        height: 70px;
        max-height: 70px;
      }

      &:after {
        bottom: 0;
        content: '';
        display: block;
      }
    }
  }

  &__inner {
    transition: all .5s ease-in-out .5s;
    overflow: hidden;
    margin: 0 auto;
    height: 48px;

    @include breakpoint($tablet) {
      height: 68px;
    }

    .global-header & {
      opacity: 0;
    }

    .global-header .open & {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &__input {
    background-color: transparent;
    border: 0;
    border-radius: 0;
    font-size: 2rem;
    line-height: 2.5;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;

    &:focus {
      box-shadow: none;
    }
  }

  .wp-content & {
    margin: 5rem auto 0;
    max-width: calc(660px + 8rem);
  }
}
