// From http://codepen.io/vajkri/details/NxzZwL/
@mixin stagger-anim-delay($i, $initial-delay, $stagger-delay) {
  @while $i > 0 {
    &:nth-child(#{$i}) {
      animation-delay: $initial-delay + $stagger-delay * $i;
    }

    $i: $i - 1;
  }
}

.flyout-menu {
  position: fixed;
  z-index: 9;
  top: 60px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow-x: auto;
  opacity: 0;
  visibility: hidden;
  background-color: $purple;
  box-shadow: 0 5px 5px rgba(0, 0, 0, .15);
  font-family: $heading;
  font-feature-settings: $fontFeatures-heading;
  transition: all .2s ease-in;
  transform: translate3d(-100%, 0, 0);

  @include breakpoint($tablet) {
    top: 70px;
    max-width: 480px;
  }

  // Animations
  //
  @include breakpoint($tablet) {
    &.open {
      .email-signup > *,
      .explore-navigation > *,
      .explore-navigation__list-item,
      .primary-navigation__list-item,
      .social-navigation > * {
        opacity: 0;
        animation: .3s anim-in ease both;
      }

      .email-signup > *,
      .explore-navigation > *,
      .social-navigation > * {
        @include stagger-anim-delay(2, .25s, .1s);
      }

      .primary-navigation__list-item {
        @include stagger-anim-delay(5, 0s, .1s);
      }

      .explore-navigation__list-item {
        @include stagger-anim-delay(2, 0s, .1s);
      }
    }
  }

  &.open {
    transition: all .4s ease-out;
    opacity: 1;
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }

  &__inner {
    transition: all .5s ease-in-out;
  }

  p {
    @include font-size(16);
    margin: 0;
    color: $white;
  }

  a {
    color: $white;

    &:active,
    &:focus,
    &:hover {
      text-decoration: none;
      color: $white;
      box-shadow: none;
    }
  }
}

@keyframes anim-in {
  0% {
    transform: translateX(-200%);
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
