.article-header {
  &__title {
    padding-top: 20px;
  }

  &__description {
    @include font-size(12, 14);

    @include breakpoint($desktop) {
      @include font-size(16, 28);
    }
  }

  &__byline {
    font-weight: 700;
  }

  // &__author {
  //   &:link {
  //     border-color: transparent;
  //   }
  // }

  // BASIC ARTICLE HEADER
  &--basic & {
    &__image-wrapper {
      height: 0;
      padding-bottom: 37.5%;
      overflow: hidden;
      position: relative;

      img {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    &__heading {
      @include breakpoint($desktop) {
        padding-top: 55px;
        margin-bottom: .5em;
      }
    }

    &__description {
      margin-bottom: 2em;
    }

    &__category {
      font-family: $heading;
      font-feature-settings: $fontFeatures-heading;
    }

    &__author,
    &__category {
      &:link {
        font-weight: 700;
        color: $purple;
        border-color: transparent;

        &:hover {
          box-shadow: none;
          background-color: transparent;
          border-bottom: 1px dotted;
        }
      }
    }

    .category--green & {
      &__category {
        color: $green;
      }
    }

    .category--teal & {
      &__category {
        color: $teal;
      }
    }

    .category--blue & {
      &__category {
        color: $blue;
      }
    }

    .category--plum & {
      &__category {
        color: $plum;
      }
    }
  }

  // ADVANCED ARTICLE HEADER
  &--advanced {
    @include aspect-ratio(75, 76);
    padding: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 0;
    width: 100%;
    max-height: 50vh;

    @include breakpoint($tablet) {
      @include aspect-ratio(16, 11);
      max-height: 60vh;
    }

    @include breakpoint($desktop) {
      @include aspect-ratio(48, 23);
      max-height: 80vh;
    }
  }

  &--advanced & {
    &__category {
      display: block;
      text-align: center;
      width: 100%;
      bottom: 0;
      padding: 5px 0;
      position: absolute;
      background-color: $purple;

      a {
        @include font-size(16);
        color: $white;
        font-family: $heading;
        font-feature-settings: $fontFeatures-heading;
        margin: 0 auto;
      }
    }

    .category--green & {
      &__category {
        background-color: $green;
      }
    }

    .category--teal & {
      &__category {
        background-color: $teal;
      }
    }

    .category--blue & {
      &__category {
        background-color: $blue;
      }
    }

    .category--plum & {
      &__category {
        background-color: $plum;
      }
    }

    &__wrapper {
      position: absolute;
      background-color: rgba(0, 0, 0, .5);
      top: inherit;
      bottom: 0;
      left: 0;
      transform: none;
      height: auto;
      width: 100%;
      max-width: 100%;
      padding: 0;
      z-index: 5;
    }

    &__title {
      color: $white;
      // max-width: 740px;
      display: block;
      margin: 0 auto 10px;
    }

    &__content {
      text-align: center;
      color: $white;
      padding: 0 1em 4em; // padding-bottom to allow space for .article-header--advanced__category
    }

    &__description {
      color: #bababa;
      display: inline-block;
      margin: 10px 0 0;
    }

    &__author,
    &__author a {
      color: $white;
      border-color: transparent;
    }
  }
}
