.story {
  margin-bottom: 20px;
  overflow: hidden;
  position: relative;

  &__image-background {
    background-position: 50% 50%;
    background-size: cover;
  }

  &__inner {
    position: relative;
    overflow: hidden;
    display: block;
  }

  &__headline {
    a {
      font-weight: 700;
    }
  }

  &__category,
  &__description {
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
  }

  &__description {
    @include font-size(16);
    display: block;

    @include breakpoint($large-tablet) {
      @include font-size(18);
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  // LIST VIEW
  .list-view & {
    overflow: hidden;
    width: 100%;
    margin-bottom: 0;
    // padding-top: 15px;

    &:focus {
      box-shadow: none;
    }

    // &:not(:first-child) {
    //   padding-top: 15px;
    // }

    &:last-child {
      margin-bottom: 15px;
    }

    &__inner {
      @include container(1260px);
      transition: all .4s ease-in-out;
      border-top: 1px solid $gray-medium;
      clear: both;
      padding: 3rem;
      width: 100%;
    }

    &:first-child .story__inner {
      border-top: 0;
    }

    &:hover .story__inner,
    &:focus .story__inner {
      background-color: $white;
      // background-color: rgba(#e5e5e5, .5);
      border-top-color: transparent;
      transform: translateY(-2px);
    }

    &__content {
      display: block;
      padding: 1rem 0;

      &:active {
        text-decoration: none;
      }
    }

    &__headline {
      @include font-size(18, 23);

      @include breakpoint($large-tablet) {
        @include font-size(30, 36);
      }

      a {
        color: $black;
      }
    }

    &__description {
      max-width: 30em;
    }

    &__image-background {
      @include aspect-ratio(1, 1);
      float: right;
      width: span(4);
      max-width: 210px;
      margin-left: $spacing-unit;

      @include breakpoint($large-mobile) {
        @include aspect-ratio(16, 9);
      }

      @include breakpoint($tablet) {
        @include aspect-ratio(18, 11);
        max-width: 360px;
      }
    }

    &--category-blue {
      .story__headline a:hover {
        color: $blue;
      }
    }

    &--category-green {
      .story__headline a:hover {
        color: $green;
      }
    }

    &--category-teal {
      .story__headline a:hover {
        color: $teal;
      }
    }

    &--category-plum {
      .story__headline a:hover {
        color: $plum;
      }
    }

    .video-player {
      @include aspect-ratio(1, 1);
      float: right;
      width: span(4);
      max-width: 210px;
      margin-left: $spacing-unit;

      @include breakpoint($large-mobile) {
        @include aspect-ratio(16, 9);
        max-width: 360px;
      }
    }

    &__category {
      font-weight: 700;
    }
  }

  // CARD VIEW
  .card-view & {
    width: span(12);

    @include breakpoint($tablet) {
      // width: span(4);
      width: span(6);
    }

    @include breakpoint($desktop) {
      width: span(4);
    }

    &__inner {
      height: 100%;
    }

    .no-js & {
      flex: 0 1 auto;
    }

    &__image-background {
      @include aspect-ratio(9, 10.4);
      height: 100%;
      transition: transform 1s ease-in;
      will-change: transform;
      transform: scale(1.001);

      @include breakpoint($tablet) {
        @include aspect-ratio(9, 10.4);
      }
    }

    &__content {
      // min-height: 185px;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      min-height: 40%;
      max-height: 50%;
      padding: 15px 15px 30px;
      background-color: rgba(0, 0, 0, .5);
      color: $white;
      transition-property: background-color;
      transition-timing-function: ease-in-out;
      transition-duration: .5s;
      display: flex;
      flex-direction: column;
      transform: translate3d(0, 0, 0);
      will-change: background-color;

      @include breakpoint($tablet) {
        min-height: 200px;
        padding: 20px 20px 30px;
      }

      &--hidden {
        opacity: 0;
        pointer-events: none;
      }
    }

    &:focus,
    &:hover {
      .story__image-background {
        transition: all 8s ease-out;
        transform: scale(1.1);
      }

      .story__content {
        transition-duration: 1s;
        background-color: $purple;
      }

      .story__category {
        background-color: darken($purple, 5%);
      }
    }

    &__headline {
      @include font-size(24);
      line-height: 1;
      color: $white;
      margin-bottom: .5em;

      a {
        color: $white;
      }
    }

    &__description {
      line-height: 1.3;
      color: $white;
    }

    &__category {
      @include font-size(14);
      background-color: $purple;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 5px 30px;
      color: $white;
      font-family: $heading;
      font-weight: normal;
      transition: background-color .3s ease-in-out;
    }

    &--video {
      &,
      &:hover {
        transform: none !important;
      }

      &:after {
        @extend %iconStyles;
        content: '\e910';
        position: absolute;
        display: block;
        top: 25%;
        left: 50%;
        transform: translate3d(-50%, -25%, 0);
        color: $white;
        font-size: 6rem;
        line-height: 1;
        box-shadow: 0 0 6rem #333;
        height: 6rem;
        width: 6rem;
        border-radius: 100%;
      }
    }

    &--featured.story--promotional {
      background-color: $white;
    }


    // CATEGORY COLORS
    &--promotional {
      &:hover {
        .story__content {
          background-color: $white !important;
        }
      }
    }

    &--category-blue {
      .story__inner,
      .story__category {
        background-color: $blue;
      }

      &:hover {
        .story__content {
          background-color: $blue;
        }

        .story__category {
          background-color: darken($blue, 5%);
        }
      }
    }

    &--category-green {
      .story__inner,
      .story__category {
        background-color: $green;
      }

      &:hover {
        .story__content {
          background-color: $green;
        }

        .story__category {
          background-color: darken($green, 5%);
        }
      }
    }

    &--category-teal {
      .story__inner,
      .story__category {
        background-color: $teal;
      }

      &:hover {
        .story__content {
          background-color: $teal;
        }

        .story__category {
          background-color: darken($teal, 5%);
        }
      }
    }

    &--category-plum {
      .story__inner,
      .story__category {
        background-color: $plum;
      }

      &:hover {
        .story__content {
          background-color: $plum;
        }

        .story__category {
          background-color: darken($plum, 5%);
        }
      }
    }
  }

  // FEATURED
  &--featured {
    .story__inner {
      padding: 0 20px 20px;
    }

    &__heading {
      @include font-size(30);
      letter-spacing: -.003em;
      font-weight: 700;
      margin: .5em 0 .75em;
    }

    &__list {
      list-style: none;
      margin: 0;
    }

    &__list-item {
      margin: 0 2em 2em 0;
    }

    &__list-item-heading {
      @include font-size(18, 20.6);
      font-weight: 500;
      margin-bottom: .25em;

      a {
        color: $black;

        &:hover {
          color: $purple-action;
        }
      }
    }

    &__list-item-category {
      @include font-size(12, 15);
      font-family: $heading;
      font-feature-settings: $fontFeatures-heading;
      font-weight: 700;
      margin: 0;

      &.category-blue {
        color: $blue;
      }

      &.category-green {
        color: $green;
      }

      &.category-teal {
        color: $teal;
      }

      &.category-plum {
        color: $plum;
      }
    }

    .card-view & {
      background: transparent;
    }
  }

  // PROMOTIONAL
  &--promotional {
    .list-view & {
      .story__inner {
        background-color: rgba($white, .9);
        border-top: 0;
        padding: $spacing-unit;

        // @include breakpoint($tablet) {
        //   padding: $spacing-unit ($spacing-unit * 2);
        // }
      }
    }

    .story__content {
      background-color: rgba($white, .9);

      ul {
        list-style-type: none;
        margin: 0;
      }

      .story__description,
      .story__headline {
        color: $black;
      }
    }
  }

  // Blank
  &--blank {
    overflow: visible;

    .story__content {
      background-color: $white;
      padding: 0 !important;
      height: 100%;
      max-height: none;
      justify-content: space-around;
    }

    &:hover .story__content {
      background-color: $white !important;
    }

    .story__figure {
      color: $black;
      text-align: center;
      padding: 0 2%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }

    .story__image {
      margin: 0 auto;
      width: auto;
      max-width: 75%;
      // max-width: 100%;

      // @include breakpoint(600px 799px) {
      //   // max-width: 50%;
      // }
    }

    .story__headline,
    .story__description {
      font-weight: 300;
      text-align: center;
      color: $copy;
      padding: 4% 2% 0;
      // margin: 0;
    }

    .story__headline {
      @include font-size(34, 50);
    }

    .story__description {
      @include font-size(18);
    }
  }

  .sidebar & {
    .story__image-background {
      background-position: center top;
      background-repeat: no-repeat;
      background-size: cover;
      height: 180px;
      width: 100%;
    }

    .story__headline {
      @include font-size(20, 24);
      margin-bottom: .5em;
    }

    .story__description {
      @include font-size(14, 20);
      margin: 0;
    }

    .story__content {
      background: $white;
      padding: 20px 30px;
    }
  }

  .vis & {
    transition: opacity .3s ease-out, transform .1s ease-in;
    transform-origin: top;
    opacity: 1;
    will-change: transform, opacity;
    // transform: scale(1);

    &.is-hidden {
      opacity: 0;
      transform: scale(.9);

      &:nth-child(2n+2) {
        transition-delay: .2s;
      }
    }

    &.is-visible {
      opacity: 1;
      transform: scale(1);
    }
  }
}

.story--featured_topic {
  font-family: $fallbackSans;

  .fonts-loaded & {
    font-family: $heading;
  }

  .story__content {
    max-height: none;
    top: 0;
    background-color: rgba($purple, .8);
  }

  &:hover .story__content {
    background-color: rgba($purple, .8) !important;
  }

  .story--featured__heading {
    color: #fff;
    font-size: 4rem;
    line-height: 1;
  }

  a {
    color: #fff;
    font-weight: 500;
    border-bottom: 1px dotted transparent;
    padding-bottom: .1em;

    &:hover {
      border-color: currentColor;
    }
  }

  .flag {
    background-color: $yellow;
    color: $black;
    margin-left: -20px;
    padding: .5em;
    max-width: 200px;
    padding-left: 20px;
    font-style: italic;
    font-weight: 400;
    font-size: 1.1em;
  }

  .story--featured__list {
    margin-bottom: 1em;
  }

  .story--featured__list-item {
    font-size: 1.3em;
    margin: 0;
    padding: 1em 0;
    // margin-right: 3em;

    &:first-child {
      border-bottom: 1px solid;
    }
  }

  .see-all {
    padding-top: 1em;
  }

}
