// Coming Soon page

.comingSoon {
  padding-top: 0;
  overflow: auto;
  display: flex;

  * {
    font-feature-settings: 'kern' on, 'liga' on, 'calt' on, 'case', 'lnum';
  }

  &:before {
    background: radial-gradient(ellipse, rgba($black, .6), $black);
    opacity: .1;
  }

  .fonts-loaded & {
    font-family: $heading;
  }

  &,
  main {
    min-height: 100%;
    background-color: #fff;
  }

  main {
    margin: auto;
    padding: 3rem 0;
    // overflow: auto;
  }

  section {
    margin-bottom: 4rem;

    @include breakpoint($tablet) {
      margin-bottom: 3rem;
    }
  }

  .logo {
    max-width: 300px;
    margin: 0 auto 2rem;

    @include breakpoint($tablet) {
      max-width: none;
    }
  }

  .lede {
    @include breakpoint($tablet) {
      max-width: 960px;
    }

    p {
      font-size: 2.2rem;
      line-height: 1.3;
    }

    h2 {
      font-size: 5rem;
      line-height: 1;
    }
  }

  .countdown {
    position: relative;
    padding: 0;

    &:after {
      position: absolute;
      content: '';
      display: block;
      height: 1px;
      bottom: 0;
      left: 50%;
      width: 90%;
      background-color: #d9d9d9;
      transform: translate3d(-50%, 2rem, 0);

      @include breakpoint($tablet) {
        display: none;
      }
    }
  }

  .signup {
    p {
      font-weight: 500;
      font-size: 2rem;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      max-width: 350px;
    }
  }

  .email-signup {
    width: 100%;
    margin: auto;

    @include breakpoint($tablet) {
      max-width: 350px;
    }
  }

  .form-group.email [type='email'] {
    background-color: #d3d3d3;
    border-color: #b8b8b8;
  }

  ::-webkit-input-placeholder {
    color: rgba($copy, .7);
  }

  .social-navigation {
    p {
      font-size: 2rem;
      margin-bottom: 1em;
      margin-right: 0;

      @include breakpoint($tablet) {
        margin-right: 2em;
        margin-bottom: 0;
      }
    }

    .social-navigation__list {
      width: 80%;
      display: flex;
      justify-content: space-around;
      margin: auto;

      @include breakpoint($tablet) {
        width: auto;
        display: inline-block;
      }
    }
  }
}

// Countdown Timer

.time_circles {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 25%;
  display: flex;
  align-items: stretch;
  align-content: center;

  canvas {
    flex: 0;
    position: absolute;
    width: 100%;
  }

  > div {
    position: absolute;
    text-align: center;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    flex: 1;
    height: 100% !important;
    top: 0 !important;
    width: 25% !important;

    > * {
      display: block;
      flex: 0 1 auto;
      margin: 0;
      padding: 0;
      font-weight: bold;
      line-height: 1 !important;
      text-align: center;
      text-transform: uppercase;
    }
  }
}

