//=====================================
//    #CORE/HTML - IMAGES
//
//  1. Fluid images for responsive purposes.
//  2. Offset `alt` text from surrounding copy.
//  3. Maintain aspect ratio
//  4. Why is a border showing up in IE9 by default? Regardless, this
//     `border: 0` gets rid of that globally on all vanilla img tags.
//=====================================

img,
svg {
  width: 100%; // [1]
  font-style: italic; // [2]
  display: block;
  border: 0; // [4]
}

img {
  height: auto; // [3]
}

// see styles/6_vendor/_wordpress-core.scss for similar WP-related styles.
//
figure {
  @include breakpoint($tablet) {
    &.left {
      float: left;
      width: 50%;
      margin-right: 1em;
    }

    &.right {
      float: right;
      width: 50%;
      margin-left: 1em;
    }
  }

  figcaption {
    font-style: italic;
  }
}

// .ceros-wrapper,
.embed-container {
  position: relative;
  height: 0;
  overflow: hidden;
}

.embed-container {
  padding-bottom: 56.25%;
  max-width: 100%;
  margin-bottom: 2rem;

  figure > & {
    margin-bottom: 0;
  }

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

