.story-swiper {
  &.swiper-container {
    overflow: visible;
    // padding: 2%;

    // overflow: hidden;
    // margin-left: -$spacing-unit;
    // margin-right: -$spacing-unit;

    @include breakpoint($tablet) {
      overflow: visible;
      margin-right: 0;
      margin-left: 0;
    }
  }

  .swiper-wrapper {
    justify-content: space-between;
  }

  .swiper-slide {
    margin-bottom: 0;
  }
}
