.hamburger-menu {
  width: 60px;
  height: 60px;
  padding: 22px 18px;
  margin: 0;
  border: 0;
  border-radius: 0;
  border-right: 1px solid #eee;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;

  @include breakpoint($tablet) {
    padding: 26px 20px;
    width: 70px;
    height: 70px;
  }

  &,
  &:hover,
  &:active {
    background-color: transparent;
  }

  // &:focus {
  //   box-shadow: 0 0 15px -5px $purple;
  // }

  // Contains the burger spans
  &__icon {
    display: block;
    position: relative;
    height: 100%;

    &:focus {
      box-shadow: none;
    }
  }

  span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    left: 0;
    border: 0;
    border-radius: 4px;
    background-color: $purple-action;
    transition: all .3s ease-in-out;

    &:nth-child(1),
    &:nth-child(4) {
      opacity: 1;
      transform: translateX(0);
      will-change: width, transform, opacity;
      transition-duration: .1s;
    }

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(4) {
      bottom: 0;
    }

    &:nth-child(2),
    &:nth-child(3) {
      top: 50%;
      transform: translateY(-50%) rotate(0deg);
      will-change: transform;
    }
  }

  &.open {
    background-color: darken($purple-action, 15%);
    border-color: darken($purple-action, 15%);
    outline-color: $purple-action;
    color: $white;

    &:focus {
      box-shadow: none;
    }

    span {
      background-color: $white;
    }

    span:nth-child(1),
    span:nth-child(4) {
      width: 0;
      opacity: 0;
      transform: translateX(-100%);
      transition-duration: .2s;
    }

    span:nth-child(2) {
      transform: translateY(-50%) rotate(45deg);
    }

    span:nth-child(3) {
      transform: translateY(-50%) rotate(-45deg);
    }

    // span:nth-child(4) {
    //   width: 0;
    //   left: 50%;
    //   opacity: 0;
    // }
  }
}
