//=====================================
//    #UTILITY - ASPECT RATIO
//=====================================
// Keep a specified aspect ratio.
// E.g. @include aspect-ratio(16, 9);

@mixin aspect-ratio($width: false, $height: false) {
  position: relative;

  > * {
    display: block;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    width: 100%;
    height: 100%;
  }

  @if $width == false and $height == false {
    &:before {
      display: none;
    }

    > * {
      transform: none;
    }
  } @else {
    &:before {
      display: block;
      content: '';
      width: 100%;
      padding-top: $height / $width * 100%;
    }

    > * {
      position: absolute;
    }
  }
}
