//=====================================
//    #CORE/HTML - HEADING ELEMENTS
//=====================================
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $black;
  font-family: $fallbackSans;
  // font-weight: 400;

  .fonts-loaded & {
    font-family: $heading;
    font-feature-settings: 'kern' on, 'liga' on, 'calt' on, 'ss02', 'lnum';
    font-weight: bold;
  }
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  letter-spacing: -.025em;
}

h1 {
  margin-top: 0;
  font-size: 3rem;

  @include breakpoint($tablet) {
    font-size: 4rem;
  }

  @include breakpoint($desktop) {
    font-size: 4.5rem;
  }
}

h2 {
  font-size: 4.236rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.618rem;
  // margin: 0;
}

h5 {
  font-size: 1.818rem;
}

.heading {
  font-weight: 400;
}

.fonts-loaded {
  .font-sans {
    font-family: $sans !important;
  }

  .font-heading,
  .font-heading-med {
    font-family: $heading !important;
  }

  .font-heading-med {
    font-weight: 500;
  }
}
