.article {
  padding-bottom: 65px;

  @include breakpoint($desktop) {
    padding-bottom: 0;
  }

  article {
    z-index: 1;
    overflow: hidden;
    position: relative;

    p a {
      transition: all .3s ease-in-out;
      border-bottom: 1px dotted;

      &:active,
      &:focus,
      &:hover {
        box-shadow: 1px 0 0 1px rgba($purple, .8), -1px 0 0 1px rgba($purple, .8);
        border-bottom-color: transparent;
        background-color: rgba($purple, .8);
        outline: none;
        color: $white;
      }
    }

    footer {
      margin-top: 0 !important;
      margin-bottom: 4rem;
    }
  }
}
