.video-player {
  @include aspect-ratio(16, 9);
  height: 100%;
  position: relative;
  overflow: hidden;
  max-width: 100%;

  @include breakpoint(max-width $tablet) {
    .card-view & {
      @include aspect-ratio(9, 10.4);
    }
  }

  iframe {
    &:-webkit-full-screen {
      transform: none;
    }
  }
}
