.global-footer {
  background-color: $gray-medium;
  margin: 0;
  padding: 0 0 20px;

  @include breakpoint($tablet) {
    margin: 0;
    padding: 30px 0;
  }

  .wrapper {
    @include breakpoint(max-width $large-tablet) {
      padding: 0;
    }

    @include breakpoint($large-tablet) {
      display: flex;
    }

    + .wrapper {
      margin-bottom: 0;
    }
  }

  p {
    @include font-size(16);
  }

  &__column {
    padding: 10px 25px;
    position: relative;
    border-bottom: 1px solid darken($gray-light, 15%);

    @include breakpoint($large-tablet) {
      @include span(4);
      border: 0;
      padding: 0 20px;

      &:after {
        content: '';

        @include breakpoint($large-tablet) {
          position: absolute;
          border-right: 1px solid darken($gray-light, 15%);
          // right: 0;
          // top: 50px;
          // height: 150px;
          // height: calc(100% - 1em);
          right: -10px;
          top: 1em;
          bottom: 1em;
        }
      }

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        @include last;
        margin: 0;
        padding-right: 0;
      }
    }

    &:last-child {
      float: left;
      border: 0;

      &:after {
        border: 0;
      }
    }
  }

  &__logo {
    // max-width: 290px;

    @include breakpoint($large-desktop) {
      padding-top: 15px;
    }
  }

  &__copyright {
    @include font-size(12);
    color: $gray-dark;
    margin: 0;
    text-align: center;

    @include breakpoint($large-tablet) {
      text-align: left;
    }
  }

  .heading {
    @include font-size(22);
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
    font-weight: 500;
    padding-top: 1em;
    margin-bottom: .25em;
  }
}
