// Dots-bg mixin
//

@mixin dots-bg($fixed: true, $rev: null) {
  position: relative;
  overflow: hidden;
  z-index: 0;
  transform: translate3d(0, 0, 0);

  &:after,
  &:before {
    position: absolute;
    overflow: hidden;
    display: block;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    transform: translate3d(0, 0, 0);
    will-change: transform;
  }

  &:after {
    @if $rev {
      background-image: linear-gradient(transparent, $purple);
    } @else {
      background-image: linear-gradient($purple, transparent);
    }
    z-index: -1;
  }

  &:before {
    background-image: url('../images/dots-bg.svg');
    background-position: center;
    background-size: 400px;
    opacity: .3;
    z-index: -2;

    @if $fixed {
      background-attachment: fixed;
    }
  }
}

.dots-bg,
%dots-bg {
  @include dots-bg;
}
