a img .align,
.align {
  &left,
  &right,
  &center,
  &none {
    margin-bottom: 1em;
  }

  @include breakpoint($tablet) {
    &left {
      float: left;
      width: 50%;
      margin-right: 1em;
    }

    &right {
      float: right;
      width: 50%;
      margin-left: 1em;
    }

    &center,
    &none {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

