.sticky-article-nav {
  background-color: $black;
  bottom: 0;
  display: none;
  height: 70px;
  left: 0;
  position: fixed;
  right: 0;
  z-index: 9;

  @include breakpoint($desktop) {
    bottom: auto;
    height: 76px;
    top: 0;
  }

  &__headline {
    display: none;

    @include breakpoint($desktop) {
      @include font-size(24, 65);
      margin-bottom: 0;
      display: block;
      color: $white;
      float: left;
      max-width: 65%;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
