.form-builder {
  .page-hero {
    padding-bottom: ($spacing-unit * 4);
    z-index: 1;

    @include breakpoint($tablet) {
      padding-bottom: ($spacing-unit * 8);
    }

    .wrapper {
      max-width: 780px;
      padding: 0;
    }

    // &__inner {}
    // &__title {}
    // &__subtitle {}
  }

  main {
    overflow: visible;

    > .wrapper {
      @include container(930px);
      // max-width: 930px;
      background: $white;
      border-radius: 6px;
      z-index: 2;
      position: relative;
      padding: ($spacing-unit) ($spacing-unit * 2) 0;
      transform: translateY(-($spacing-unit * 2));

      @include breakpoint($tablet) {
        transform: translateY(-($spacing-unit * 4));
        padding: ($spacing-unit * 2) ($spacing-unit * 6) 0;
      }
    }

    iframe {
      margin: 30px 0;
      width: 100%;

      @include breakpoint($desktop) {
        height: 480px;
        margin: 40px -60px;
        width: 120%;
      }
    }

    img {
      margin: 30px 0;
      width: 100%;

      @include breakpoint($desktop) {
        height: auto;
        margin: 40px -60px;
        width: 120%;
      }
    }
  }
}
