.category-header {
  @include dots-bg;
  background-color: $purple;
  color: $white;
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 0;

  @include breakpoint($desktop) {
    text-align: left;
  }

  &__inner {
    @include container;
    padding-top: 3rem;
    padding-bottom: 2rem;

    @include breakpoint($desktop) {
      padding-top: 6rem;
      padding-bottom: 4rem;
    }
  }

  &__headline {
    font-size: 3.5rem;
    line-height: 1;
    font-weight: 500;
    letter-spacing: -.05em;
    margin-bottom: 14px;
    color: $white;

    @include breakpoint($desktop) {
      @include span(5 of 12);
      font-size: 4rem;
      margin-bottom: 0;
    }
  }

  &__description {
    @include breakpoint($desktop) {
      @include span(7 of 12);
      margin: 0;
    }

    p {
      font-size: 2rem;
      font-family: $heading;
      font-feature-settings: $fontFeatures-heading;
      color: $white;
      margin-bottom: 0;
    }
  }

  .category--blue & {
    background-color: $blue;

    &:after {
      background-image: linear-gradient($blue, transparent);
    }
  }

  .category--green & {
    background-color: $green;

    &:after {
      background-image: linear-gradient($green, transparent);
    }
  }

  .category--plum & {
    background-color: $plum;

    &:after {
      background-image: linear-gradient($plum, transparent);
    }
  }

  .category--teal & {
    background-color: $teal;

    &:after {
      background-image: linear-gradient($teal, transparent);
    }
  }

  &-special {
    @include dots-bg($rev: true);
    min-height: 40vh;
    background-size: cover;
    background-position: 0;
    display: flex;
    align-items: center;

    @include breakpoint($desktop) {
      align-items: flex-end;
    }

    &:before {
      opacity: .1;
    }
  }
}
