.article--advanced {
  main {
    padding-top: 2em;

    @include breakpoint($desktop) {
      padding-top: 4em;
    }
  }

  .wp-content {
    .q-and-a,
    p,
    h1,
    h2,
    h3,
    h4,
    h5,
    blockquote,
    ul {
      @include breakpoint($desktop) {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  .lede {
    display: block;
  }

  .poll {
    @include breakpoint($desktop) {
      width: 40%;
      margin-left: 1em;
    }
  }
}

.intro {
  background-color: #fff;
  width: 100%;

  @include breakpoint($tablet) {
    padding: 3rem 0;
  }

  .heading {
    @include font-size(20);
    font-family: $heading;
    font-weight: 500;
    display: block;
    border-bottom: 1px solid palette(gray, button);
    padding-bottom: 1em;
    margin: 1em 0;
  }
}

.snapshot,
.takeaways {
  padding: 2%;
}

.snapshot {
  @include breakpoint(max-width $tablet) {
    width: 100%;
    margin: 0 0 1em;
  }

  .stats {
    text-align: center;

    @include breakpoint($tablet) {
      text-align: left;
    }
  }

  .stat,
  .stat-description {
    display: inline-block;
    vertical-align: top;
  }

  .stat {
    padding: 0 1rem 1rem 0;
  }

  .stat-description {
    @include font-size(20, 24);
    text-align: center;

    @include breakpoint($desktop) {
      text-align: left;
    }
  }
}

.stat {
  font-size: 100px;
  line-height: .8;
  font-family: $heading;
  font-weight: 300;
  width: 100%;

  sup {
    font-size: 50%;
  }
}

.c-txtimage {
  background-size: cover;
  position: relative;
  max-height: 75vh;
  height: 100%;
  overflow: hidden;

  &:before {
    display: block;
    content: '';
    width: 100%;
    padding-top: 56.25%;
  }

  &--left {
    &:before {
      background-image: linear-gradient(to right, rgba($black, .8), transparent 80%);
    }

    .text {
      left: 0;
    }
  }

  &--right {
    &:before {
      background-image: linear-gradient(to left, rgba($black, .8), transparent 80%);
    }

    .text {
      right: 0;
    }
  }

  .text {
    position: absolute;
    color: #fff;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;

    @include breakpoint($tablet) {
      width: 60%;
    }

    p {
      max-width: none;
    }
  }
}

.stats--large {
  @include dots-bg;
  background-color: $blue-bright;
  color: #fff;
  padding: 5rem 0 0;

  @include breakpoint($tablet) {
    padding: 8rem 0 4rem;
  }

  .stat {
    text-align: center;
  }

  &:after {
    background-image: linear-gradient(rgba($blue-bright, .9) 20%, transparent);
  }

  .row,
  .item {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .row {
    justify-content: center;

    @include breakpoint($tablet) {
      flex-direction: row;
    }
  }

  .item {
    flex: 1;
    align-self: stretch;
    margin-bottom: 3rem;

    span {
      margin-bottom: 3rem;
    }
  }

  .stat-icon {
    max-width: 32px;
  }
}
