//=====================================
//    #CORE/HTML - TABLES
//=====================================

table {
  @include font-size(15, 21);
  width: 100%;
  border: 1px solid $gray-medium;

  @include breakpoint($desktop) {
    @include font-size(20, 31);
  }
}

tbody,
caption {
  font-feature-settings: 'kern' 1, 'liga' 1, 'calt' 1, 'pnum' 0, 'tnum' 1, 'onum' 0, 'lnum' 1, 'zero' 1;
}

tr,
td,
th {
  padding: 10px 20px;

  @include breakpoint($desktop) {
    padding-left: 30px;
    padding-right: 30px;
  }
}

tr {
  background: $white;

  &:nth-child(odd) {
    background: $gray-light;
  }

  &:last-child:not(:only-child) {
    border-bottom: 2px solid $gray-medium;
  }
}

td {
  border-right: 2px solid $gray-light;
  vertical-align: top;

  &:last-child {
    border: 0;
  }
}

th {
  background: darken($gray-light, 5%);
  border-right: 2px solid $white;
  font-family: $heading;
  font-weight: 400;

  &:last-child {
    border: 0;
  }
}

.table {
  overflow: auto;
  padding-bottom: 2rem;

  &.themed {
    thead th {
      background: $blue;
      color: $white;
      text-align: left;
    }

    tr {
      background: $white;

      b {
        color: $orange;
      }

      &:nth-child(odd) {
        background: lighten($blue, 65%);
      }

      &:last-child:not(:only-child) {
        border-bottom: 2px solid $gray-medium;
      }
    }
  }

  &.themed-center {
    table {
      border: 0;
    }

    span {
      color: $blue;
    }

    tr,
    th {
      background: transparent;
      border-bottom: 0;

      &:nth-child(odd) {
        background: transparent;
      }
    }

    td,
    th {
      border-right: 2px solid $blue;

      &:last-child {
        border-right: 0;
      }
    }

    tr:last-child {
      td {
        vertical-align: top;
      }
    }

    thead th {
      background: transparent;
      padding-bottom: 30px;
    }

    th {
      color: $orange;
      vertical-align: top;
    }

    td {
      width: 33%;
      vertical-align: middle;
      text-align: center;
    }

    tr td img {
      display: block;
      height: auto;
      margin: 0 auto;
      max-width: 100%;
      width: auto;
    }
  }

  figcaption {
    text-align: left;
  }
}

