.stories-container {
  padding: 2%;

  &__inner {
    position: relative;
    // display: flex;
    // flex-wrap: wrap;
    // justify-content: space-between;
  }

  &:not([data-module='swiperGrid']) {
    .stories-container__inner {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &.list-view {
    max-width: 100%;
  }

  &__gutter {
    width: gutter();
  }

  &__column {
    @include breakpoint($tablet) {
      width: span(6);
    }

    @include breakpoint($desktop) {
      width: span(4);
    }
  }
}
