@mixin button($color: $purple-action, $text: $white) {
  background-color: $color;
  border: 1px solid darken($color, 5%);
  color: $text;

  &:hover {
    background-color: darken($color, 5%);
    color: darken($text, 5%);
  }
}

button,
.button {
  @include button;
  font-family: $heading;
  font-feature-settings: $fontFeatures-heading;
  transition: all .1s ease-in;
  font-size: 1em;
  border-radius: 6px;
  display: inline-block;
  margin: 1rem 0;
  padding: 10px 2rem;

  &:not(.icon--circle) {
    &.icon {
      &:before {
        margin-right: .5em;
      }
    }

    &.icon--after {
      &:after {
        margin-left: .5em;
      }
    }
  }

  &.icon-only {
    padding-left: 1em;
    padding-right: 1em;
  }

  &:hover,
  &:active {
    color: $white;
    text-decoration: none;
  }
}

.button--white {
  @include button($white, $black);
}

.button--gray {
  @include button(palette(gray, button), $purple-action);
}

.button--clear {
  @include button(rgba($gray-dark, 0), $black);

  &:hover {
    background-color: $gray-light;
    color: currentColor;
  }
}

.icon--circle {
  @extend %icon-only;
  border-radius: 50%;
  border: 0;
  padding: 0;
  height: 50px;
  width: 50px;
}

%icon-only,
.icon-only {
  position: relative;
  text-indent: -999em;

  &:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    text-indent: 0;
    line-height: 1;
    padding: 0;
    margin: 0;
  }
}
