.comment {
  display: block;
  overflow: hidden;
  width: 100%;
  margin-bottom: 20px;

  p {
    max-width: none !important;
  }

  &__article {
    padding: 20px !important;
    background: $white;
    border-radius: 5px;
  }

  &__author {
    @include font-size(22, 48);
    font-family: $heading;
    font-feature-settings: $fontFeatures-heading;
    overflow: hidden;
    // margin-bottom: 15px;

    .fn {
      font-weight: 700;
    }
  }

  &__avatar {
    width: auto;
    float: left;
    margin-right: 15px;
    border-radius: 50%;
    max-width: 48px;
  }

  &__meta {
    font-size: 90%;
    margin-left: 65px;
  }

  .children {
    margin-top: 20px;
  }
}
