//=====================================
//    #CORE - ICONS
//=====================================

$icons: (
  arrow-down: '\e900',
  arrow-left: '\e902',
  arrow-right: '\e903',
  arrow-up: '\e901',
  calendar: '\e905',
  charts: '\e912',
  clipboard: '\e908',
  email: '\e90a',
  facebook: '\e60a',
  google-plus: '\e90b',
  grid: '\e916',
  home: '\e904',
  instagram: '\e90c',
  interactives: '\e913',
  linkedin: '\e90d',
  list: '\e917',
  pinterest: '\e911',
  pocket: '\e90e',
  search: '\e914',
  share: '\e909',
  thumbs-down: '\e906',
  thumbs-up: '\e907',
  twitter: '\e61a',
  videos: '\e915',
  youtube: '\e90f',
  play: '\e910'
);

%iconStyles {
  speak: none;
  text-transform: none;
  font-size: inherit;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  line-height: inherit;
  vertical-align: bottom;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // visibility: hidden;

  // .fonts-loaded & {
  font-family: 'icomoon';
  visibility: visible;
  // }
}

// For adding font icons to elements using CSS pseudo-elements
// http://jaydenseric.com/blog/fun-with-sass-and-font-icons
@mixin icon($icon: false, $position: before, $styles: false) {
  @if $position == both {
    $position: 'before, &:after';
  }

  // Either a :before or :after pseudo-element, or both, defaulting to :before
  &:#{$position} {

    @if $icon {
      // A particular icon has been specified
      content: '#{map-get($icons, $icon)}';
    }

    @if $styles {
      @extend %iconStyles;
    }

    // Include any extra rules supplied for the pseudo-element
    @content;
  }
}

// Set the required styles on all icons
[class^='icon-'],
[class*=' icon-'],
.icon {
  &:not(.icon--after) {
    @include icon($styles: true);
  }

  &.icon--after {
    @include icon($position: after, $styles: true);

    &:before {
      display: none;
    }
  }
}

// Setup a class name for each icon
@each $name, $char in $icons {
  .icon-#{$name}:not(.icon--after) {
    @include icon(#{$name}, $position: before);
  }

  .icon-#{$name}.icon--after {
    @include icon(#{$name}, $position: after);
  }

  %icon-#{$name} {
    content: '#{map-get($icons, $name)}';
  }
}

.icon-thumbs-up-1 {
  @include icon(thumbs-up);
}

.icon-thumbs-down-1 {
  @include icon(thumbs-down);
}
