//=====================================
//  #CORE/HELPERS - HIDDEN
//
// This class is intended to be used on links
// that should not display their text, ex. a icon button.
//=====================================

.hidden {
  height: 1px;
  left: -10000px;
  overflow: hidden;
  position: absolute;
  top: auto;
  width: 1px;
}

.visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 0 !important;
  width: 0 !important;
  margin: -1px !important;
  padding: 0 !important;
  border: 0 !important;
  line-height: 0;
}
