.comments {
  padding: 20px 0 0;
  border-top: 1px solid;

  .tags + & {
    border-top: 0;
  }

  // .heading {
  //   @include font-size(32, 45);
  //   font-weight: 400;
  // }

  .accordion-toggle,
  .comment-reply-link {
    float: right;
  }

  .content {
    clear: right;
    margin-top: 3rem;
  }

  .list {
    margin: 0;
    list-style-type: none;
  }

  .add-comment {
    display: inline-block;
    margin: .75em 0;
    font-weight: 700;
  }

  .comment__reply {
    @extend %clearfix;
  }

  .comment__header {
    margin-bottom: 2em;
  }

  .required {
    color: $error;
  }
}

.comment-form {
  legend {
    margin-bottom: 3rem;

    p {
      margin-bottom: 0;
      max-width: none;
      font-size: .9em;
      line-height: 1.4;
    }
  }

}
