.page-cta {
  @include dots-bg;
  padding: 50px 15px;
  text-align: left;

  &.dark-purple {
    background-color: palette(purple, dark);
  }

  &__title {
    @include font-size(40, 48);
    font-weight: 500;
    color: $white;
    margin-top: 0;
  }

  &__subtitle {
    @include font-size(20);
    color: $white;
  }
}
