.endnotes {
  padding: 30px 0 0;

  &__title {
    @include font-size(16, 15.5);
    @include breakpoint($desktop) {
      @include font-size(24, 31);
    }
    font-family: $sans;
    margin-bottom: 20px;
  }

  ol {
    @include ol-custom(endnotes);
  }

  li {
    @include font-size(15);
    @include breakpoint($desktop) {
      @include font-size(20);
    }
  }
}
