.logo {
  margin: 0;

  &__link {
    display: block;
    height: 100%;
    width: 100%;
  }

  svg,
  img {
    max-width: 100%;
  }

  .global-header & {
    transition: all .3s ease-out;
    max-width: 150px;
    margin: auto;
    height: 100%;

    @include breakpoint($tablet) {
      margin: auto;
      padding: 5px 0;
      max-width: 225px;
    }

    svg {
      height: 100%;
    }

    .tagline {
      transition: all 1s ease-out;
      opacity: 0;
    }
  }
}
