//=====================================
//    #CORE/HTML - PAGE
//=====================================

* {
  box-sizing: border-box; // establishes border-box model for all elements
  word-wrap: normal;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); // Remove default tappable touch color in iOS
}

html {
  height: 100%;
  min-height: 100vh;

  &.flyout-menu-open {
    overflow: hidden;
  }
}

body {
  @include font-size(16, 24);
  transition: padding .5s ease-in-out;
  font-family: $fallbackSans;
  overflow-x: hidden;
  padding-top: 60px;
  background-color: $gray-light;
  font-feature-settings: 'kern' on, 'liga' on, 'calt' on, 'lnum';

  @include breakpoint($tablet) { // .flyout-menu-open overlay
    padding-top: 70px;

    &:before {
      transition: all .3s ease-in-out;
      content: '';
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(0, 0, 0, .5);
      z-index: 6;
      pointer-events: none;
      opacity: 0;
    }

    &.flyout-menu-open {
      overflow: hidden;

      &:before {
        transition: all .3s ease-in-out;
        width: 100%;
        height: 100%;
        opacity: 1;
      }
    }
  }

  .fonts-loaded & {
    font-family: $sans;
    font-weight: 300;
  }
}

main {
  background-color: $gray-light;
  overflow: hidden;
  position: relative;
  z-index: 1;
  transform: translate3d(0, 0, 0);
}
