fieldset {
  border: 0;
  padding-left: 0;
  margin: 0;
  // border: 1px solid #c0c0c0;
  // margin: 0 2px;
  // padding: .35em .625em .75em;
}

select,
input {
  // @include font-size(16, 24);
}

input,
select,
textarea,
.file-custom, {
  margin: 1rem;
  margin-left: 0;
  padding: 12px 2rem;
  font-family: $heading;
}

input,
select,
textarea,
.file-custom,
.control-indicator {
  display: block;
  background-color: lighten($gray-light, 2%);
  border-radius: $border-radius;
  border: 1px solid $gray-medium;
  // line-height: 2;
  width: 100%;

  &:focus {
    @extend %input-focus;
  }

  &.error {
    background-color: $errorBg !important;
    border: 1px solid $error !important;
    color: $error !important;

    &:focus {
      box-shadow: 0 0 1px 1px $error !important;
      outline: 1px $error !important;
    }

    &::placeholder {
      color: $error !important;
    }
  }
}

[type='submit'],
[type='radio'],
[type='checkbox'] {
  @include transition;
}

[type='submit'] {
  cursor: pointer;
  width: auto;

  &:hover {
    background-color: darken($gray-light, 5%);
  }
}

[type='color'] {
  width: 7rem;
  height: 6rem;
  padding: .5rem;
}

[type='range']:focus {
  box-shadow: none;
  background-color: transparent;
}

