label,
.label {
  @include font-size(16);
  color: $copy;
  display: block;
  margin-left: 1em;
  font-weight: bold;

  .helper {
    font-weight: 400;
    display: block;
    color: $gray;
  }

  &.error {
    color: $error !important;
  }
}

form .row {
  margin: 2em 0;
}

// For pairing form elements without gap between
.form-group {
  position: relative;

  input,
  button {
    margin-right: 0;
    display: inline-block;
    vertical-align: middle;
    height: 50px;

    &:first-child {
      border-radius: $border-radius 0 0 $border-radius;
    }

    &:last-child {
      border-radius: 0 $border-radius $border-radius 0;
    }
  }
}

// Custom form styling via 'WTF, forms?' (http://wtfforms.com/)
.control {
  position: relative;
  cursor: pointer;
  padding-left: 2.5em;
  margin-bottom: 1em;

  &.checkbox,
  &.radio {
    font-weight: inherit;
  }

  input {
    position: absolute;
    opacity: 0;
    z-index: -1;
    height: 0;
    width: 0;
  }

  input:focus ~ .control-indicator {
    box-shadow: 0 0 0 1px rgba($purple, .5);
    border-color: transparent;
  }

  input:active ~ .control-indicator {
    color: #fff;
    background-color: rgba($purple, .5);
  }

  // Checked state
  input:checked ~ .control-indicator {
    color: #fff;
    background-color: $purple;
    outline: none;
  }

  // Checkbox SVG
  &.checkbox input:checked ~ .control-indicator {
    background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCA4IDgiPjxwYXRoIGZpbGw9IiNGRkYiIGQ9Ik02LjQgMWwtLjcuNy0yLjggMi44LS44LS44LS43LS43TDAgNC40bC43LjcgMS41IDEuNS43LjcuNy0uNyAzLjUtMy41LjctLjdMNi40IDF6Ii8+PC9zdmc+);
  }

  &.radio input:checked ~ .control-indicator {
    color: #fff;
    background-color: #fff;
    border: 7px solid $purple;
  }
}

.control-indicator {
  transition: all .1s ease-in;
  position: absolute;
  left: 0;
  width: 1.5em;
  height: 1.5em;
  text-align: center;
  background-color: #fff;
  background-size: 70%;
  background-position: 50%;
  background-repeat: no-repeat;
  user-select: none;
  padding: 0;
  top: 50%;
  transform: translateY(-50%);

  // Checkbox modifiers
  .checkbox & {
    border-radius: $border-radius;
  }
  // Radio modifiers
  .radio & {
    border-radius: 50%;
  }
}

.select {
  position: relative;

  @include icon(arrow-down, after, true) {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1.5em;
    pointer-events: none;
  }

  select {
    display: inline-block;
    width: 100%;
    border-radius: 6px;
    cursor: pointer;
    outline: 0;
    appearance: none;
  }

  // Undo the Firefox inner focus ring
  select:focus:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000;
  }

  // Active/open
  select:active {
    color: #fff;
    background-color: $purple;
  }

  // Hide the arrow in IE10 and up
  select::-ms-expand {
    display: none;
  }
}

.file {
  position: relative;

  input {
    opacity: 0;
    height: 100%;
    cursor: pointer;
  }

  // Focus
  input:focus ~ .file-custom {
    box-shadow: 0 0 0 1px rgba($purple, .5);
  }

  &:hover {
    .file-custom:after {
      background-color: darken($purple-action, 15%);
    }
  }
}

.file-custom {
  transition: all .1s ease-in;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  overflow: hidden;
  bottom: 0;
  margin: 0;
  pointer-events: none;

  &:before {
    content: 'Choose file...';
    color: #a9a9a9;
  }

  &:after {
    transition: all .1s ease-in;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 6;
    display: block;
    content: 'Browse';
    padding: 10px 1em;
    color: #fff;
    background-color: $purple-action;
  }
}

.form-disabled {
  opacity: .75;
  cursor: auto;
  pointer-events: none;
}


// wordpress custom plugin //
.gform_wrapper {
  form li {
    display: inline-block;
    width: 100%;
    margin-bottom: 40px;
  }

  input,
  select,
  textarea,
  .file-custom {
    font-family: $heading;
    margin: 1rem;
    margin-left: 0;
    padding: 12px 2rem;
    text-indent: inherit;
    margin-bottom: 0;
  }

  ul {
    list-style: none;
    margin: 0;
  }

  .gform_wrapper .gfield_required {
    color: #544a5b;
  }

  p {
    font-size: 1.6rem;
    line-height: 2.4rem;
  }

  label {
    margin-top: 0;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: #544a5b;
    display: block;
    margin-left: 1em;
    font-weight: bold;
  }

  .chosen-container.chosen-container-single {
    .chosen-single {
      appearance: none;
      background: #f7f7f7;
      border-radius: 6px;
      border: 1px solid #e4e4e4;
      cursor: pointer;
      display: block;
      display: inline-block;
      font-family: $heading;
      height: auto;
      line-height: inherit;
      margin: 1rem;
      margin-bottom: 0;
      margin-left: 0;
      outline: 0;
      padding: 12px 2rem;
      position: relative;
      text-indent: inherit;
      width: 100%;

      @include icon(arrow-down, after, true) {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 1.5em;
        pointer-events: none;
      }

      div {
        display: none;
      }

      span {
        display: block;
        overflow: hidden;
        margin-right: 26px;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 14px;
        font-family: $heading;
        color: #666;
      }
    }

    .choose-results {
      li {
        font-size: 16px;
        font-family: $heading;
      }
    }
  }

  .ginput_container_checkbox,
  .ginput_container_radio {
    br {
      display: none;
    }

    li {
      margin-bottom: 0;
    }

    input {
      display: inline-block;
      width: 16px;
      margin-left: 17px;
    }

    label {
      display: inline-block;
      margin-left: 0;
    }
  }

  .gfield_description {
    margin-top: 0;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: #544a5b;
    display: block;
    margin-left: 1em;
    font-weight: normal;
  }

  .gform_footer.top_label {
    display: block;
    margin: 0 auto;
    position: relative;
    text-align: center;
    width: 100%;

    @include breakpoint($tablet) {
      width: 50%;
    }

    @include breakpoint($desktop) {
      width: 30%;
    }

    &:after,
    &:before {
      display: none;

      @include breakpoint($tablet) {
        background-color: darken($gray-light, 10%);
        content: '';
        display: block;
        height: 1px;
        position: absolute;
        top: 50%;
        width: 100%;
      }
    }

    &:after {
      right: -2em;
      transform: translate3d(100%, -50%, 0);
    }

    &:before {
      left: -2em;
      transform: translate3d(-100%, -50%, 0);
    }

    input[type="submit"] {
      font-size: 1.4rem;
      line-height: 2.1rem;
      border: 1px solid #d9d9d9;
      font-family: $heading;
      font-weight: 500;
      letter-spacing: 1px;
      outline: 10px solid transparent;
      padding: 1.25em 1em;
      text-transform: uppercase;
      display: inline-block;
      width: 100%;
      box-sizing: border-box;
      left: 0;
      margin: 1rem 0;

      &.more {
        color: $purple-action;
        background-color: transparent;

        &:hover {
          background-color: darken($gray-light, 10%);
        }
      }
    }

    br {
      display: none;
    }
  }

  .name-field {
    label:first-child {
      display: none;
    }

    .ginput_container_name {
      label {
        margin-bottom: -25px;
      }

      p {
        float: left;
        margin-right: 1.78572%;
        margin-bottom: -33px;
        width: 49.10714%;

        &:last-child {
          margin-right: 0;
        }

        span {
          width: 100%;
        }
      }
    }
  }
}
