.card-swiper {
  .card {
    background: $white;

    &__image-background {
      background-position: center top;
      background-repeat: no-repeat;
      background-size: cover;
      height: 240px;
      width: 100%;
    }

    &__headline {
      @include font-size(20, 25);
      font-weight: 500;
      margin: 0 0 15px;
    }

    &__content {
      background: $white;
      padding: 30px;
    }

    &__description {
      @include font-size(16, 25);
      margin-bottom: 0;
      max-width: 100%;
    }
  }

  &.card-container {
    overflow: hidden;
    margin-left: -$spacing-unit;
    margin-right: -$spacing-unit;

    @include breakpoint($tablet) {
      overflow: visible;
      margin-right: 0;
      margin-left: 0;
    }
  }
}
