.reading-progress {
  bottom: 0;
  height: 5px;
  left: 0;
  position: absolute;
  width: 100%;

  @include breakpoint($tablet) {
    height: 6px;
  }

  &__progress-bar {
    appearance: none;
    background-color: transparent;
    border: 0;
    bottom: 0;
    color: $purple;
    height: 6px;
    position: absolute;
    width: 100%;

    .category--green & {
      color: $green;

      &::-webkit-progress-bar {
        background-color: transparent;
      }

      &::-webkit-progress-value {
        background-color: $green;
      }

      &::-moz-progress-bar {
        background-color: $green;
      }
    }

    .category--blue & {
      color: $blue;

      &::-webkit-progress-bar {
        background-color: transparent;
      }

      &::-webkit-progress-value {
        background-color: $blue;
      }

      &::-moz-progress-bar {
        background-color: $blue;
      }
    }

    .category--teal & {
      color: $teal;

      &::-webkit-progress-bar {
        background-color: transparent;
      }

      &::-webkit-progress-value {
        background-color: $teal;
      }

      &::-moz-progress-bar {
        background-color: $teal;
      }
    }

    .category--plum & {
      color: $plum;

      &::-webkit-progress-bar {
        background-color: transparent;
      }

      &::-webkit-progress-value {
        background-color: $plum;
      }

      &::-moz-progress-bar {
        background-color: $plum;
      }
    }
  }
}
