.social-navigation {
  p {
    display: inline-block;
    vertical-align: middle;
    margin-right: 2em;
    margin-bottom: 0;
  }

  &__list {
    vertical-align: middle;
  }

  &__link {
    @extend %icon-only;
    display: block;
    width: 40px;
    height: 40px;
    font-size: 20px;

    &:before {
      line-height: 1;
      margin: 0;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }

    &:active,
    &:hover {
      text-decoration: none;
    }

    &:focus {
      box-shadow: none;
    }
  }

  .flyout-menu &,
  .global-footer & {
    .social-navigation__link {
      // transform: scale(1);
      transform-origin: center;
      will-change: transform, color;

      &:active,
      &:focus,
      &:hover {
        transform: scale(1.2);
      }
    }
  }

  .global-footer & {
    p {
      @include font-size(16);
      margin-top: 1.5em;
      display: block;
    }
  }

  .flyout-menu & {
    border-top: 1px solid darken($purple, 5%);
    padding: 1em 2em 1.5em;

    .social-navigation__link {
      font-size: 20px;

      &:active,
      &:focus,
      &:hover {
        color: $white;
      }
    }
  }
}
