// TABS
.tabs-container {
  box-shadow: 0 6px 6px -4px palette(gray, button);

  .select,
  select {
    @include breakpoint($tablet) {
      display: none;
    }
  }

  select {
    background-color: $gray-medium;
  }

  [role='tablist'] {
    display: none;
    list-style-type: none;
    margin: 0;
    padding: 0;
    margin-bottom: -1px;

    @include breakpoint($tablet) {
      display: flex;
      flex-direction: row;
    }

    li {
      flex: 1;
      padding-bottom: 5px;
      border: 1px solid transparent;
      margin-right: 5px;

      &:last-child {
        margin-right: 0;
      }

      a {
        background-color: $gray-medium;
        position: relative;
        display: block;
        padding: 1em 0;
        text-align: center;
        color: $black;

        &:focus {
          box-shadow: none;
          text-decoration: none;
          background-color: $gray-light;
        }
      }

      &.active {
        border-color: #ccc;
        border-bottom-color: #fff;
        background-color: #fff;

        a {
          background-color: inherit;
        }
      }
    }
  }

  .tab-content {
    background-color: #fff;
    // margin-bottom: 2em;
    padding: 2rem 1rem;

    @include breakpoint($tablet) {
      padding: 3rem 1rem;
      border: 1px solid #ccc;
      border-bottom-color: #e5e5e5;
    }


    &[aria-hidden='false'] {
      display: block;
      visibility: visible;
    }

    &[aria-hidden='true'] {
      display: none;
      visibility: hidden;
    }
  }
}
