//=====================================
//    #CORE - WRAPPER
//=====================================

// Note: The ':root' selector allows you to target the highest-level 'parent'
//  * element in the DOM, or document tree.
//  * (https://css-tricks.com/almanac/selectors/r/root)

:root {
  background-color: $gray-light;
  color: $copy;
  position: relative;
}

.wrapper {
  @include container(1260px);
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  width: 100%;

  @include breakpoint($tablet) {
    padding-right: $spacing-unit * 2;
    padding-left: $spacing-unit * 2;
  }

  .article & {
    @include container(1060px);
  }

  & + & {
    margin: $spacing-unit auto ($spacing-unit * 2);
  }
}

.wrapper-full {
  padding: 0;
  max-width: none;

  .article & {
    @include container(100%);
  }
}
